      <van-radio-group
        v-model="extensionList.extensionType"
        @change="typeChange()"
      >
        <van-radio :name="1">延期本次保養日期</van-radio>
        <select
          v-model="extensionList.extensionDate"
          placeholder="選擇延期月數"
          v-if="extensionList.extensionType == 1"
        >
          <option value="" disabled selected style="display:none;"
            >選擇延期月數</option
          >
          <option value="2">兩個月</option>
          <option value="3">三個月</option>
        </select>
        <!-- <textarea name="" id="" placeholder="請填寫原因"></textarea> -->
        <van-radio :name="2">中止定期保養</van-radio>
        <textarea
          name=""
          id=""
          placeholder="請填寫原因"
          v-model="extensionList.extensionReason"
        ></textarea>
      </van-radio-group>
