        <span @click="StockUpShowClose()">取消</span>
        <em @click="stockUpHidePopup()">儲存</em>
      </div>
    </van-popup>
    <!-- 终止定期保养确认弹窗 -->
    <van-popup v-model="comfirmShow" class="InstallBasicsBoxcomfirmPopup" :close-on-click-overlay="false">
      <h1>此選項為整台淨水器中止保養</h1>
      <h1>的回覆，請確認後填寫中止原因，謝謝</h1>
      <button @click="comfirmShow = false">確定</button>
    </van-popup>
    <!-- 完成弹窗 -->
    <van-popup v-model="SuccessShow" class="InstallBasicsBoxPopupSuccess">
      <img draggable="false" src="@/assets/image/success.png" alt="" />
      <h2>完成</h2>
      <div @click="hidePopup()">返回濾心更換列表</div>
    </van-popup>
