</template>

<script>
import {
  POST_GetIncident,
  POST_UpdateUnMaintainIncident
} from "@/utils/api";
import HeaderNavigation from "@/components/HeaderNavigation";
export default {
  name: "InstallBasics",
  components: {
    HeaderNavigation
  },
  data() {
    return {
      extensionList: {
        CustId: this.$route.query.CustId,
        extensionType: 1,
        extensionDate: "",
        extensionReason: ""
      },
      CustId: "",
      StockUpShowNum: "",
      Filter1Life: "",
      Filter2Life: "",
      Filter3Life: "",
      UnChangeCodeList: [],
      userInfoList: {},
      // 更换弹窗
      StockUpShow: false,
      // 确认弹窗
      comfirmShow: false,
      // 完成弹窗
      SuccessShow: false,
      ProductCarouselImgRed: {
        backgroundImage:
          "url(" + require("@/assets/image/InstallBasicsRed.png") + ")",
        backgroundRepeat: "no-repeat",
        backgroundSize: "100% 100%"
      },
      ProductCarouselImgOrange: {
        backgroundImage:
          "url(" + require("@/assets/image/InstallBasicsOrange.png") + ")",
        backgroundRepeat: "no-repeat",
        backgroundSize: "100% 100%"
      },
      ProductCarouselImgBlue: {
        backgroundImage:
          "url(" + require("@/assets/image/InstallBasicsBlue.png") + ")",
        backgroundRepeat: "no-repeat",
        backgroundSize: "100% 100%"
      }
    };
  },
  watch: {
    StockUpShow: function() {
      if (this.StockUpShow == false) {
        this.extensionList = {
          CustId: this.$route.query.CustId,
          extensionType: 1,
          extensionDate: "",
          extensionReason: ""
        };
      }
    }
  },
  created() {
    this.GetIncident();
  },
  methods: {
    // 获取信息
    GetIncident() {
      var UofIds = {
        DistributionUofId: sessionStorage.getItem("DistributionUofId")
      };
      POST_GetIncident(UofIds).then(response => {
        for (let i = 0; i < response.Data.length; i++) {
          if (response.Data[i].IncidentNo == this.$route.query.CustId) {
            this.CustId = response.Data[i].CustId;
            this.userInfoList = response.Data[i];
            var a = response.Data[i].IncidentDate.split("T")[0];
            var s = a.split("-");
            this.userInfoList.IncidentDate =
              s[0] + "年" + s[1] + "月" + s[2] + "日";

            if (!this.userInfoList.Filter1Life) {
              this.userInfoList.Filter1Life = "0%";
            }
            if (!this.userInfoList.Filter2Life) {
              this.userInfoList.Filter2Life = "0%";
            }
            if (!this.userInfoList.Filter3Life) {
              this.userInfoList.Filter3Life = "0%";
            }
            if (!this.userInfoList.Filter4Life) {
              this.userInfoList.Filter4Life = "0%";
            }
            if (!this.userInfoList.Filter5Life) {
              this.userInfoList.Filter5Life = "0%";
            }
            this.Filter1Life = this.userInfoList.Filter1Life.split("%")[0];
            this.Filter2Life = this.userInfoList.Filter2Life.split("%")[0];
            this.Filter3Life = this.userInfoList.Filter3Life.split("%")[0];
            this.Filter4Life = this.userInfoList.Filter4Life.split("%")[0];
            this.Filter5Life = this.userInfoList.Filter5Life.split("%")[0];
          }
        }
      });
    },
    // 延期中止状态变换
    typeChange() {
      // 清空日期選擇框
      this.extensionList.extensionDate = "";
      if (this.extensionList.extensionType == 2) {
        this.comfirmShow = true;
      }
    },
    // extensionReason
    stockUpshowPopup(value) {
      if (this.userInfoList.IsLock) {
        return false;
      }
      // 數據回顯
      if (value == 1) {
        if (this.$store.state.extension.extension1 && this.$store.state.extension.extension1.CustId == this.$route.query.CustId) {
          this.extensionList.extensionDate = this.$store.state.extension.extension1.extensionDate
          this.extensionList.extensionType = this.$store.state.extension.extension1.extensionType
          this.extensionList.extensionReason = this.$store.state.extension.extension1.extensionReason
        }
      } else if (value == 2) {
        if (this.$store.state.extension.extension2 && this.$store.state.extension.extension2.CustId == this.$route.query.CustId) {
          this.extensionList.extensionDate = this.$store.state.extension.extension2.extensionDate
          this.extensionList.extensionType = this.$store.state.extension.extension2.extensionType
          this.extensionList.extensionReason = this.$store.state.extension.extension2.extensionReason
        }
      } else if (value == 3) {
        if (this.$store.state.extension.extension3 && this.$store.state.extension.extension3.CustId == this.$route.query.CustId) {
          this.extensionList.extensionDate = this.$store.state.extension.extension3.extensionDate
          this.extensionList.extensionType = this.$store.state.extension.extension3.extensionType
          this.extensionList.extensionReason = this.$store.state.extension.extension3.extensionReason
        }
      } else if (value == 4) {
        if (this.$store.state.extension.extension4 && this.$store.state.extension.extension4.CustId == this.$route.query.CustId) {
          this.extensionList.extensionDate = this.$store.state.extension.extension4.extensionDate
          this.extensionList.extensionType = this.$store.state.extension.extension4.extensionType
          this.extensionList.extensionReason = this.$store.state.extension.extension4.extensionReason
        }
      } else if (value == 5) {
        if (this.$store.state.extension.extension5 && this.$store.state.extension.extension5.CustId == this.$route.query.CustId) {
          this.extensionList.extensionDate = this.$store.state.extension.extension5.extensionDate
          this.extensionList.extensionType = this.$store.state.extension.extension5.extensionType
          this.extensionList.extensionReason = this.$store.state.extension.extension5.extensionReason
        }
      }
      // 打開彈窗
      this.StockUpShow = true;
      // 第幾道濾心
      this.StockUpShowNum = value;
    },
    // 关闭更換弹窗
    StockUpShowClose() {
      this.StockUpShow = false;
    },
    // 儲存更換
    stockUpHidePopup() {
      // 延期更換
      if (this.extensionList.extensionType == 1) {
        // 第幾道濾心更換
        if (this.StockUpShowNum == 1) {
          // 濾心延期儲存數據到vuex
          this.$store.commit("extension/setExtension1", this.extensionList);
          // 濾心延期時清空更換儲存的數據
          this.$store.commit("replace/setReplace1", {});
        } else if (this.StockUpShowNum == 2) {
          this.$store.commit("extension/setExtension2", this.extensionList);
          this.$store.commit("replace/setReplace2", {});
        } else if (this.StockUpShowNum == 3) {
          this.$store.commit("extension/setExtension3", this.extensionList);
          this.$store.commit("replace/setReplace3", {});
        } else if (this.StockUpShowNum == 4) {
          this.$store.commit("extension/setExtension4", this.extensionList);
          this.$store.commit("replace/setReplace4", {});
        } else if (this.StockUpShowNum == 5) {
          this.$store.commit("extension/setExtension5", this.extensionList);
          this.$store.commit("replace/setReplace5", {});
        }
        this.StockUpShow = false;
      } else {
        if (!this.extensionList.extensionReason) {
          return this.$notify({ type: 'warning', message: '請填寫原因' });;
        }
        // 中止更換
        let dataForm = {
          IncidentNo: this.userInfoList.IncidentNo,
          StopMaintainMemo: this.extensionList.extensionReason
        };
        POST_UpdateUnMaintainIncident(dataForm).then(res => {
          this.StockUpShow = false;
          this.SuccessShow = true;
          this.GetIncident();
        });
      }
    },
    hidePopup() {
      this.SuccessShow = false;
    },
    // 跳转到保养历程
    toInstallCourse() {
      this.$router.push({
        path: "/InstallReplacement/InstallCourse",
        query: {
          CustId: this.userInfoList.IncidentNo,
          id: this.userInfoList.IncidentNo,
          WaterMachineMfno: this.userInfoList.WaterMachineMfno
        }
      });
      // this.$router.push(
      //   "/InstallReplacement/InstallCourse?CustId=" +
      //     this.CustId +
      //     "&id=" +
      //     this.$route.query.CustId +
      //     "&WaterMachineMfno=" +
      //     this.userInfoList.WaterMachineMfno
      // );
    },
    // 跳轉到開始更換
    toInstallScanningFilter(Value) {
      if (this.userInfoList.IsLock) {
        return false;
      }
      this.$router.push({
        path: "/InstallReplacement/InstallBasics/ScanningFilter",
        query: {
          CustId: this.userInfoList.IncidentNo,
          value: Value,
          WaterMachineMfno: this.userInfoList.WaterMachineMfno
        }
      });
    },
    // 客戶簽名結案
    SignTheCase() {
      // 判斷當前值是否為當前濾心的，不是則清空
      if (this.$store.state.replace.replace1 && this.$store.state.replace.replace1.CustId != this.$route.query.CustId) {
        this.$store.commit("replace/setReplace1", {});
      }
      if (this.$store.state.replace.replace2 && this.$store.state.replace.replace2.CustId != this.$route.query.CustId) {
        this.$store.commit("replace/setReplace2", {});
      }
      if (this.$store.state.replace.replace3 && this.$store.state.replace.replace3.CustId != this.$route.query.CustId) {
        this.$store.commit("replace/setReplace3", {});
      }
      if (this.$store.state.replace.replace4 && this.$store.state.replace.replace4.CustId != this.$route.query.CustId) {
        this.$store.commit("replace/setReplace4", {});
      }
      if (this.$store.state.replace.replace5 && this.$store.state.replace.replace5.CustId != this.$route.query.CustId) {
        this.$store.commit("replace/setReplace5", {});
      }
      if (this.$store.state.extension.extension1 && this.$store.state.extension.extension1.CustId != this.$route.query.CustId) {
        this.$store.commit("extension/setExtension1", {});
      }
      if (this.$store.state.extension.extension2 && this.$store.state.extension.extension2.CustId != this.$route.query.CustId) {
        this.$store.commit("extension/setExtension2", {});
      }
      if (this.$store.state.extension.extension3 && this.$store.state.extension.extension3.CustId != this.$route.query.CustId) {
        this.$store.commit("extension/setExtension3", {});
      }
      if (this.$store.state.extension.extension4 && this.$store.state.extension.extension4.CustId != this.$route.query.CustId) {
        this.$store.commit("extension/setExtension4", {});
      }
      if (this.$store.state.extension.extension5 && this.$store.state.extension.extension5.CustId != this.$route.query.CustId) {
        this.$store.commit("extension/setExtension5", {});
      }
      // 判斷當前值是否為當前濾心的，不是則清空

      // 判斷哪道濾心爲空，爲空不能提交。必須全填
      if (this.userInfoList.HasFilter1 && this.userInfoList.Filter1NeedToChange && !this.$store.state.extension.extension1.CustId && !this.$store.state.replace.replace1.CustId) {
        return this.$toast("濾心1號尚未選擇更換");
      }
      if (this.userInfoList.HasFilter2 && this.userInfoList.Filter2NeedToChange && !this.$store.state.extension.extension2.CustId && !this.$store.state.replace.replace2.CustId) {
        return this.$toast("濾心2號尚未選擇更換");
      }
      if (this.userInfoList.HasFilter3 && this.userInfoList.Filter3NeedToChange && !this.$store.state.extension.extension3.CustId && !this.$store.state.replace.replace3.CustId) {
        return this.$toast("濾心3號尚未選擇更換");
      }
      if (this.userInfoList.HasFilter4 && this.userInfoList.Filter4NeedToChange && !this.$store.state.extension.extension4.CustId && !this.$store.state.replace.replace4.CustId) {
        return this.$toast("濾心4號尚未選擇更換");
      }
      if (this.userInfoList.HasFilter5 && this.userInfoList.Filter5NeedToChange && !this.$store.state.extension.extension5.CustId && !this.$store.state.replace.replace5.CustId) {
        return this.$toast("濾心5號尚未選擇更換");
      }
      // 判斷哪道濾心爲空，爲空不能提交。必須全填

      // 價格綜合加起來
      let price = 0;
      if (this.$store.state.replace.replace1 && this.$store.state.replace.replace1.Price) {
        price = parseFloat(this.$store.state.replace.replace1.Price) + price;
      }
      if (this.$store.state.replace.replace2 && this.$store.state.replace.replace2.Price) {
        price = parseFloat(this.$store.state.replace.replace2.Price) + price;
      }
      if (this.$store.state.replace.replace3 && this.$store.state.replace.replace3.Price) {
        price = parseFloat(this.$store.state.replace.replace3.Price) + price;
      }
      if (this.$store.state.replace.replace4 && this.$store.state.replace.replace4.Price) {
        price = parseFloat(this.$store.state.replace.replace4.Price) + price;
      }
      if (this.$store.state.replace.replace5 && this.$store.state.replace.replace5.Price) {
        price = parseFloat(this.$store.state.replace.replace5.Price) + price;
      }
      // 價格綜合加起來
      // 跳轉至簽名
      this.$router.push({
        name: "Signature",
        query: {
          CustId: this.$route.query.CustId,
          type: "replace",
          Price: price,
          value: this.userInfoList.NeedToMaintainCount,
          ProductId: this.userInfoList.ProductId,
          IncidentNo: this.userInfoList.IncidentNo
        }
      });
    }
  }
};
</script>

